<template>
  <div class="header">
  <div class="container-header">
    <div class="logo">
    Neli
    </div>
    <div class="nav">
    <ul class="desktop_list">
    <li><router-link to="/"><a href="#"> О нас </a></router-link></li>
    <li><router-link to="/projects"><a href="#"> Работы </a></router-link></li>
    <li><router-link to="/contacts"><a href="#"> Контакты </a></router-link></li>
    </ul>
    </div>
    <div class="mobile_menu">
    <div class="menu_btn" v-if="!show" @click="show = !show" key="menu"> <img :src="require('./menu.png')" /></div>
    <div class="menu_btn" v-else @click="show = !show" key="clear"> <img :src="require('./close.png')" /></div>
    </div>

  </div>
  <ul v-if="show" class="mobile_list">
  <li><router-link to="/"><a @click="show = !show" href="#"> О нас </a></router-link></li>
  <li><router-link to="/projects"><a @click="show = !show" href="#"> Работы </a></router-link></li>
  <li><router-link to="/contacts"><a @click="show = !show" href="#"> Контакты </a></router-link></li>
  </ul>
  </div>
</template>

<script>
export default  {
  data () {
    return {
    show: false
    }
  },
  name: 'NavHeader'
}
</script>

<style scoped>
@media screen{
.header {
color: white;
height: auto;
width: 100%;
background-color: black;
}
.container-header{
display: flex;
align-items: center;
justify-content: center;
width: 100%;
height: 100%;
margin: auto;
}
.logo {
flex-grow: 3;
font-family: "Ubuntu";
font-size: 40px;
margin-left: 30px;
}
.mobile_menu {
display: none;
}
.nav {
flex-grow: 1;
display: flex;
justify-content: flex-end;
}
ul {
  font-family: "Roboto";
  list-style-type: none;
  padding: 0;
  margin: 0;
}

li {
  display: inline-block;
  padding: 0 20px;
  height: 40px;
  margin-top: 22px;
}

a {
text-decoration: none;
color: white;
height: 100%;
}

li:hover, a:active~li {
text-decoration: none;
color: white;
height: 36px;
border-bottom: 4px solid white;
}
}



@media (max-width: 769px) {
.logo {
margin-left: 20px;
}
.nav ul {
display: none;
}

.container-header {
margin: 10px auto;
}
.mobile_menu {
flex-grow: 1;
display: flex;
justify-content: flex-end;
margin-right: 20px;
}

.mobile_list {
display: flex;
flex-direction: column;
align-items: center;
}

.menu_btn img {
width: 30px;
}
}
</style>



